<template>
  <div class="flex-1 text-wrap py-spacing-100 text-content-on-neutral-primary">
    <h3
      class="mb-spacing-150 text-body/l/relaxed/500 text-content-on-neutral-secondary"
    >
      Origin
    </h3>

    <div
      v-for="(item, index) in labelValues"
      :key="index"
      class="neuxs:gap-spacing-100 mb-spacing-150 flex flex-col items-start justify-between text-body/m/relaxed/400 nexus:mb-spacing-0 nexus:flex-row ipad:text-body/l/relaxed/400"
    >
      <span
        class="text-left text-content-on-neutral-secondary nexus:text-content-on-neutral-primary"
        >{{ item?.label }}:</span
      >
      <span class="nexus:text-right">{{ item?.value }}</span>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    company: {
      type: Object,
      required: false,
      default: () => ({}),
    },
  },
  computed: {
    labelValues() {
      if (this.company && Object.keys(this.company)?.length > 0) {
        return [
          { label: "Origin", value: this.company?.origin },
          { label: "Add User", value: this.company?.addUser?.name },
          { label: "Origin List", value: this.company?.originList },
          {
            label: "Annual Revenue",
            value: this.$options.filters.toCurrency(
              this.company?.annualRevenue,
            ),
          },
        ];
      } else {
        return [];
      }
    },
  },
  methods: {
    LabelValue({ label, value }) {
      return (
        value && (
          <p class="flex items-center justify-between gap-spacing-25 text-body/l/relaxed/400">
            <span>{label}:</span>
            <span>{value}</span>
          </p>
        )
      );
    },
  },
};
</script>

